<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                
                <el-form-item label="保险公司" :inline="true">
                    <el-input v-model.trim="filters.insurance_name" placeholder="请输入保险公司"></el-input>
                </el-form-item>
                <el-button @click="handleSearch">查询</el-button>
                <el-button type="primary" icon="plus" @click="$router.push({ path: '/insuranceSupplier/add'})">新建</el-button>
            </el-form>
            <el-table :data="tableData" border class="table" ref="multipleTable" @selection-change="handleSelectionChange">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="insurance_name" label="保险公司" width="120">
                </el-table-column>
                <el-table-column prop="prod_num" label="启用保险产品数" width="120">
                </el-table-column>
                <el-table-column prop="receipt_account" label="付款账号" width="120">
                </el-table-column>
                <el-table-column prop="create_time" label="创建时间">
                </el-table-column>
                <el-table-column prop="create_user" label="创建人">
                </el-table-column>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-view" @click="$router.push({ path: '/insuranceSupplier/view', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">查看</el-button>
                        <el-button type="text" icon="el-icon-edit" @click="$router.push({ path: '/insuranceSupplier/edit', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">修改</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :page-size="limit" :total="total">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<script>
    import * as API from '@/api/resourceManagement'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 10,
                total: 1,
                filters: {
                    insurance_name:'',
                }
            }
        },
        created() {
            this.getData();
        },
        computed: {
            
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params = {
                    page: this.page,
                    ...this.filters
                }
                API.apiInsuranceSupplierList(params).then(res => {
                    this.tableData = res.data;
                    this.total = res.total;
                })
            },
            handleSearch() {
                this.getData();
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
